#process{
    background-color: rgb(61, 61, 61);
    border-radius:25px;
    padding:5px 20px;
    color: white;
    width: fit-content;
}
.spinner{
    width: 40px;
    height: 40px;
    border: 5px solid #f3f3f3; 
    border-top: 5px solid black;
    border-radius: 50%;
    animation: spin 2s linear infinite;

}
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }